.CheckoutModal {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 100;
  }
  
  .CheckoutModal-content {
    width: 50vw;
    height: 50vh;
    background-color: white;
    border-radius: 10px;
    position: relative;
  }
  
  .CheckoutModal-close {
    position: absolute;
    top: -20px;
    right: -20px;
    height: 25px;
    width: 25px;
    margin: 5px 5px;
    border-radius: 50%;
    background-color: black;
    color: white;
    line-height: 25px;
    cursor: pointer;
  }


